<script>
  import {
    chatLayout,
    chatTheme,
  } from '../store'

	import {
		chatMessages,
		isMessagesLoading,
	} from '../store/chat'

  import ChatMessage from './ChatMessage.svelte'
  import ChatLoader from './ChatLoader.svelte'

	let chatBotBodyScroll;
</script>

<div
  class="chatBotBody"
  style="background-color: {$chatTheme.body.backgroundColor}"
  class:layout-fixed={$chatLayout === 'fixed'}
  class:layout-static={$chatLayout === 'static'}
  class:isLoading={$isMessagesLoading}
>
  <div
    class="chatBotBodyScroll"
    bind:this={chatBotBodyScroll}
  >
    <div class="chatBotMessages">
      {#each $chatMessages as { text, type, direction, time, attachment, actions }}
      <ChatMessage
        text={text}
        type={type}
        direction={direction}
        time={time}
        attachment={attachment}
        actions={actions}
      />
      {/each}
    </div>
  </div>

  {#if $isMessagesLoading}
  <ChatLoader/>
  {/if}
</div>

<style lang="scss">.chatBotBody {
  position: relative;
  overflow: hidden;
  flex: 1;
  background-color: #f8f7f7; }

.chatBotBodyScroll {
  height: 100%;
  padding: 25px;
  overflow-y: auto;
  box-sizing: border-box;
  scrollbar-width: thin;
  scrollbar-color: #c26ad3 transparent; }
  .chatBotBodyScroll::-webkit-scrollbar {
    width: 6px; }
  .chatBotBodyScroll::-webkit-scrollbar:hover {
    cursor: pointer; }
  .chatBotBodyScroll::-webkit-scrollbar-button {
    display: none; }
  .chatBotBodyScroll::-webkit-scrollbar-thumb {
    background: #c26ad3;
    border-radius: 10px; }
  .chatBotBodyScroll::-webkit-scrollbar-thumb:hover {
    background: #666; }
  .chatBotBodyScroll::-webkit-scrollbar-track {
    background: transparent; }
  .chatBotBodyScroll::-webkit-scrollbar-corner {
    background: #999; }
  .chatBotBodyScroll::-webkit-resizer {
    background: #111; }

.chatBotMessages {
  display: flex;
  flex-direction: column; }
</style>
